import db from '@/common/services/firebaseUserService';
import { doc, updateDoc } from 'firebase/firestore';
import store from '@/store';

export async function setCurrentRaceUser(name) {
    const user = doc(db, '/users', '' + store.state.common.currentUser.id);
    await updateDoc(user, {
        raceUser: name
    });
}

export async function closeSelection(selectionIndex, pict) {
    console.log({ selectionIndex, pict });
    const sellerId = store.state.common.currentUser.id;
    const user = doc(db, '/users', '' + store.state.common.currentUser.raceUser);
    let newRaceInfo = store.state.seller.currentRace;


    if (!newRaceInfo[selectionIndex].pictures) {
        newRaceInfo[selectionIndex].pictures = {};
    }
    if (!newRaceInfo[selectionIndex].pictLinks) {
        newRaceInfo[selectionIndex].pictLinks = {};
    }
    if (newRaceInfo[selectionIndex].pictLinks[pict.original]) {
        throw new Error('Alreaady taken');
    }
    let proposedInc = 1;
    let oldPict = newRaceInfo[selectionIndex].pictures[sellerId];
    if (oldPict) {
        delete newRaceInfo[selectionIndex].pictLinks[oldPict.original];
        proposedInc = 0;
    }

    newRaceInfo[selectionIndex].pictures[sellerId] = pict;
    newRaceInfo[selectionIndex].pictLinks[pict.original] = pict;

    console.log({ newRaceInfo });

    let userInfo = store.state.seller.currentRaceUserInfo;
    if (!userInfo.sellers) userInfo.sellers = {};
    if(!userInfo.sellers[store.state.common.currentUser.id]){
        userInfo.sellers[store.state.common.currentUser.id] = {proposals:proposedInc,score:0};
    }else{
        userInfo.sellers[store.state.common.currentUser.id].proposals += proposedInc;
    }

    await updateDoc(user, {
        ...userInfo,
        race: newRaceInfo
    });
}



export default db;